.toaster-container {
  visibility: visible;
  width: 100%;
}
.toaster-container .toaster {
  margin: 0;
  padding: 0;
  list-style: none;
}
.toaster-container .toaster-countdown .badge {
  color: #ffffff;
}
.toaster-container .toaster-countdown:hover .badge {
  font-size: 0;
}
.toaster-container .toaster-countdown:hover .badge:after {
  font-size: 12px;
  content: attr(hover-text);
}
.toaster-container .alert {
  padding: 0 15px;
  margin: 0;
  border-radius: 0;
  border: 0px;
}
.toaster-container .toast {
  display: flex;
  align-items: center;
}
.toaster-container .toast > * {
  flex: 0 0 auto;
}
.toaster-container .toast > *:not(:last-child) {
  margin-right: 4px;
}
.toaster-container .toast-message {
  flex: 1 1 auto;
  margin-top: 10px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  line-height: normal;
}
.toaster-container .toast-stack {
  padding-bottom: 10px;
}
.toaster-container .toast-stack pre {
  display: inline-block;
  width: 100%;
  margin: 10px 0;
  word-break: normal;
  word-wrap: normal;
  white-space: pre-wrap;
}
.toaster-container .toast-controls {
  display: flex;
}
.toaster-container .toast-controls button {
  flex: 0 0 auto;
  border: 0;
  border-radius: 0;
  padding: 10px 15px;
}
.toaster-container .alert-success .badge {
  background: #185e36;
}
.toaster-container .alert-info .badge {
  background: #051214;
}
.toaster-container .alert-warning .badge {
  background: #7f5006;
}
.toaster-container .alert-danger .badge {
  background: #921e12;
}
.toaster-container .alert-banner {
  background-color: white;
  padding: 10px 15px;
}
.toaster-container .alert-banner a {
  color: #328caa;
}
.toaster-container .alert-banner .toaster-countdown {
  background-color: white;
}
.toaster-container .alert-banner .badge {
  background-color: #328caa;
}
.toaster-container .toast-message {
  white-space: normal;
}
.toast-message-banner {
  display: block;
}



