body {
  overflow-x: hidden;
}
.app-wrapper {
  display: flex;
  flex-flow: column nowrap;
  position: absolute;
  left: 53px;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  margin: 0 auto;
  background-color: #fff;
  /**
   * 1. Dirty, but we need to override the .is-global-nav-open state
   *    when we're looking at the log-in screen.
   */
}
.app-wrapper.hidden-chrome {
  left: 0 !important;
  /* 1 */
}
.app-wrapper .navbar-right {
  margin-right: 0;
}
.app-wrapper-panel {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  box-shadow: -4px 0px 3px rgba(0, 0, 0, 0.2);
}
.app-wrapper-panel > * {
  flex-shrink: 0;
}



